<script>
  import 'video.js/dist/video-js.css'
  import { videoPlayer } from 'vue-video-player'
export default {
    components: {
        videoPlayer
    },
    data(){
        return{
            serverTime:'',//服务器时间
            project_id: '',//获取演练id
            showTitleIndex: '0',   //当前显示的标题编号
            linkBigList: [],      //环节内容
            showBigTitle: 0,//当前是第几环节
            showClock: false,  //显示倒计时
            clockTime:'',//倒计时间
            exerciseState:'',//所有演练状态数据
            showTitleA: '',//演练状态名字
            nodeId:'',//环节id
            nodeIdArr:[],//环节id数组，用于获取总分
            groupId:'',//小组id
            problemId:'',//问题id
            showReferenceDetail: false, //显示参考答案详情
            textSrc:'',//文字素材
            videoSrc:'',//视频素材
	        videoOption: {//视频素材
		        muted: false,
		        language: 'en',
		        sources: [{
			        type: "video/mp4",
			        src: ""
		        }],
                autoplay:true,
	        },
            picSrc:'',//图片素材
            subject:{},//题目
            problemScore:'',// 题目分数
            answer:'',//用户回答的答案
            isShow:false,//是否显示提交答案弹框
            isSubmit:1,//是否主动提交过答案，若提交过，则在倒计时结束时，不再自动提交答案
            group:[],//分组结果
            groupList:[],//组名
            detailShow:false,//是否显示详细答案
            reference:'',//参考答案
            isShowReference:false,//导演端是否显示参考答案
	        isLength:false,//导演端显示答案，参考答案的长度是否已经计算出来了
            mark:'',//打分分数
            isMarkRight:false,// 评分验证
            isValidation:false,
            isSetscore:false,//是否评过分
            allAnswer:[], //展示所有得分 //所有参演组答案
            scoreLinkGroup: [],//当前环节得分
            allScore: [],//所有分组的得分
            scoreTotal: 0,    //演练总分
            ranking:'',//名次，前几甲
            time : '',//倒计时显示
            flag : false,//倒计时是否结束
            timer:'',//倒计时定时器
            time1:'',//实时获取演练状态定时器
            time2:'',//实时获取是否显示参考答案定时器
            leftTime:0,
            loadingText:'',
            hasNode: false,
            isLive:false,//直播是否已经开始
	        isStopLive:false,//直播是否已经停止
            //直播相关参数
            rtmpKey:'', //所以流列表
            pushAddr:'',//推流地址
            pullKey:'',//拉流参数
            videoOptions: {//观看直播参数
                sources: [{
                    type: "rtmp/mp4",
                    src:'',
                    withCredentials: false
                }],
                techOrder:['flash'],
                language: 'zh-CN',
                live: true,
                autoplay: true,
	            poster:"./static/waitingLive.jpeg",
                height: 1000
            }
        }
    },
    created () {
        this.$store.state.showHeader = '3';
        this.role=localStorage['roles'];//获取演练身份，参演／评分／观看／点评
        this.userId=JSON.parse(localStorage.user).id;//获取用户id
        this.project_id = localStorage['drill_project'];//获取演练id
        if(localStorage['isSubmit']){
          this.isSubmit=localStorage['isSubmit'];
        } else {
          this.isSubmit = 1;
        }
        this.getNode();
        this.getGroup();
    },
    mounted(){
        clearInterval(this.time1);
        this.time1 = setInterval(()=>{
            this.getStep();
        },2000);

        this.time2 = setInterval(()=>{
            if((this.showTitleA=='显示所有组答案'||(this.showTitleA=='开始点评'&&(this.role == '点评'||this.role == '评分点评'))||this.showTitleA=='开始打分')&&!this.isLive){
                this.getReferenceStatus();
//	            this.beginLive();
            }
            if(this.showTitleA=='开始点评'){
	            if(!this.isLive && (this.role == '点评'||this.role == '评分点评') ){//点评人直播点评
		            var swfVersionStr = "11.1.0";
		            var xiSwfUrlStr = "./static/playerProductInstall.swf";
		            var flashvars = {};
		            var params = {};
		            params.quality = "high";
		            params.bgcolor = "#ffffff";
		            params.allowscriptaccess = "sameDomain";
		            params.allowfullscreen = "true";
		            var attributes = {};
		            attributes.id = "NodePublisher";
		            attributes.name = "NodePublisher";
		            attributes.align = "middle";
		            swfobject.embedSWF(
			            "./static/NodePublisher.swf", "flashContent",
			            "250", "250",
			            swfVersionStr, xiSwfUrlStr,
			            flashvars, params, attributes);
		            this.getAllFlow();
	            } else if(this.role == '观看'||this.role == '参演'||this.role == '评分'){
	                this.videoOptions.sources[0].src = "rtmp://pili-live-rtmp.jiaxingyanlian.com/emergency-drill/php-sdk-test1517368506";
//		            this.getLivingFlow();
	            }
            }else if(this.showTitleA == '开始打分'&&!this.isStopLive){
	            var swfVersionStr = "11.1.0";
	            var xiSwfUrlStr = "./static/playerProductInstall.swf";
	            var flashvars = {};
	            var params = {};
	            params.quality = "high";
	            params.bgcolor = "#ffffff";
	            params.allowscriptaccess = "sameDomain";
	            params.allowfullscreen = "true";
	            var attributes = {};
	            attributes.id = "NodePublisher";
	            attributes.name = "NodePublisher";
	            attributes.align = "middle";
	            swfobject.embedSWF(
		            "./static/NodePublisher.swf", "flashContent",
		            "0", "0",
		            swfVersionStr, xiSwfUrlStr,
		            flashvars, params, attributes);
	            this.stopPublish();
            }
        },2000);
      },

    beforeDestroy(){
        clearInterval(this.time1);
        clearInterval(this.time2);
    },

    methods:{
    	//格式化时间
	    NewDate(str) {
		    //首先将日期分隔 ，获取到日期部分 和 时间部分
		    var day = str.split(' ');
		    //获取日期部分的年月日
		    var days = day[0].split('-');
		    //获取时间部分的 时分秒
		    var mi = day[day.length - 1].split(':');
		    //获取当前date类型日期
		    var date = new Date();
		    //给date赋值  年月日
		    date.setUTCFullYear(days[0], days[1] - 1, days[2]);
		    //给date赋值 时分秒  首先转换utc时区 ：+8
		    date.setUTCHours(mi[0] - 8, mi[1], mi[2]);
		    return date;
	    },
//        倒计时
        timeDown () {
	    	console.log('倒计时正在机型中')
            let m = this.formate(parseInt(this.leftTime/60%60));
            let s = this.formate(parseInt(this.leftTime%60));
            if(this.leftTime <= 0){//倒计时结束
                this.flag = true;
                this.$emit('time-end');
                this.clockTime='';
            }
	        this.showClock=true;
            this.time = `${m}:${s}`;
            this.leftTime = this.leftTime - 1;
        },

//        格式化时间，时间为两位
        formate (time) {
            if(time>=10){
                return time
            }else{
                return `0${time}`
            }
        },

//        获取服务器时间
        getServerTime(){
        	this.leftTime = 0;
            this.$axios.post('/api/time-end').then((res)=>{
//            	console.log(res)
	            const endTime = parseInt(this.clockTime);
                this.serverTime = this.NewDate(res.data.data);
	            this.leftTime = parseInt((endTime-this.serverTime)/1000);
            }).catch((error)=>{
            	console.log(error);
            })
        },

//        获取演练环节名称
        getNode(){
            this.nodeIdArr=[];
            this.$axios.post('/api/start',{
                "design_id":this.project_id
            }).then((res)=>{
//                console.log(res)
                this.linkBigList = res.data.data.node_name;
            }).catch((error)=>{
                console.log(error)
            })
        },

//        获取演练状态
        getStep(){
            this.$axios.post('/api/look-status',{
                "design_id":this.project_id
            }).then((res)=>{
//                console.log(res)
                this.exerciseState=JSON.parse(res.data.data.design_location);
                if(this.showTitleA !== this.exerciseState.step){//步骤不相等，则进行下一步，并且获取演练的全部状态
//                    this.$store.state.loading = true;
//                    this.isShowReference = false;
                    this.nodeId = this.exerciseState.node_id;//环节id
	                this.clockTime = this.exerciseState.end_time;//环节倒计时
                    for(let i in this.linkBigList){
                        if(this.nodeId === this.linkBigList[i].id){
                            this.showBigTitle=i;
                        }
                    }
                    this.showTitleA=this.exerciseState.step;//环节步骤
                    if(this.showTitleA == '播放素材'){
//	                    this.$store.state.loading=true;
                        this.getVideo();
                    }else if(this.showTitleA == '播放题目'){
//	                    this.$store.state.loading=true;
                        this.getTitle();
                    }else if(this.showTitleA == '开始答题'){
//	                    this.$store.state.loading = true;
                        localStorage['isSubmit'] = this.isSubmit;
                        if(localStorage['answer']){
                          this.answer=localStorage['answer'];
                                    } else {
                          localStorage['answer']=this.answer;
                        }
                        this.getTitle();
                    }else if(this.showTitleA=='显示所有组答案'||this.showTitleA=='开始点评'||this.showTitleA=='开始打分'){
//	                    this.$store.state.loading=true;
                        this.getTitle();
                    }else if(this.showTitleA=='展示得分'){
                    	this.isStopLive=false;
                    	this.isLive= false;
	                    this.isShowReference = false;
	                    this.$store.state.loading=true;
                        this.getTotalPoints();
                        this.getNodeScore();
//                        this.showRank();
                    }else if(this.showTitleA=='结束演练'){
	                    this.$store.state.loading=true;
                        this.getTotalPoints();
                        this.showRank();
                        clearInterval(this.time1);
	                    clearInterval(this.time2);
	                    localStorage.removeItem('answer');
	                    localStorage.removeItem('isSubmit');
                    }

	                this.flag = false;
                    clearInterval(this.timer);
                    this.showClock = false;
                    console.log('clocktime:'+this.clockTime)
                    if(this.clockTime){//有倒计时,依旧实时调接口
	                    this.getServerTime();//获取服务器时间
                        if(this.showTitleA == '开始答题'){
	                        this.$store.state.loading=false;
                        }
                        this.timer = setInterval(()=>{//倒计时定时器，开始倒计时
//                            console.log('开始倒计时')
                            if(this.flag === true){//倒计时结束
                                console.log('倒计时结束')
	                            this.showClock=false; //时钟显示关闭
//                                this.$store.state.loading=true;//开启loading
                                clearInterval(this.timer);//清除倒计时
                                if(this.showTitleA=='开始答题'){
//                                    console.log('开始答题状态');
                                    if(this.isSubmit == 1&&this.role=='参演'){
//                                    	console.log('没提交过答案，自动提交')
                                        this.determine();
                                    }
                                    setTimeout(this.getStep(),1500);
                                }
                            }else{//倒计时没结束
                            	console.log('倒计时没结束');
	                            this.timeDown();
	                            if(this.leftTime == 10 && (this.role == '评分'||this.role == '评分点评') && this.showTitleA == '开始打分'){//评分时间剩余十秒的时候，给评分人一个提示
		                            this.$message({
			                            message: '还剩余十秒，请尽快提交评分',
			                            type: 'warning',
                                        duration:2000,
		                            });
	                            }
                            }
                        },1000)

                    }else{//没有倒计时
                    	this.showClock=false;
                        clearInterval(this.timer);
                    }
                }
              }).catch((error)=>{
              console.log(error)
            })
        },

//        获取播放素材
        getVideo(){
            //清空素材
            this.picSrc='';
            this.videoSrc='';
	        this.videoOption.sources[0].src='';
	        this.textSrc='';
            this.$axios.post('/api/play-video',{
                "node_id":this.nodeId
            }).then((res)=> {
//                console.log(res)
                this.textSrc = this.linkBigList[this.showBigTitle].description;
                if(res.data.data){
                    //判断文件类型，是图片还是视频
                    let filePath=res.data.data[0].original_file;
                    let index= filePath.lastIndexOf(".");
                    let ext = filePath.substr(index+1);
                    if(this.isImage(ext)){//素材为图片
                        this.picSrc=filePath;
                    }else if(this.isVedio(ext)){//素材为视频
                        this.videoSrc=filePath;
                        this.videoOption.sources[0].src = this.$store.state.file_path +filePath;
                    }
                }
                this.$store.state.loading=false;
              }).catch((error)=> {
              console.log(error)
            })
        },

//      获取播放题目
        getTitle(){
	        this.reference='';
	        this.loadingText='';
            this.$axios.post('/api/play-problem',{
                "node_id":this.nodeId
            }).then((res)=>{
//                console.log(res)
//                if(res.data.data[0].problem){
                    this.subject=res.data.data[0].problem;//题目信息
//                }
                this.problemId=res.data.data[0].problem.id;//问题id
                if(this.showTitleA === '开始答题'){
                    this.getGroupId();
                }else if(this.showTitleA == '显示所有组答案'||this.showTitleA == '开始点评'){
                    if(this.showTitleA == '显示所有组答案'){
	                    this.loadingText='正在整理各组答案';
                    }
	                if(this.subject.answer){//如果有参考答案，就显示参考答案
		                this.reference=this.subject.answer;
	                }
                    this.getAllAnswers();
                }else if(this.showTitleA == '开始打分'){
	                if(this.subject.answer){//如果有参考答案，就显示参考答案
		                this.reference = this.subject.answer;
	                }
                    this.getAllAnswers();
                    this.problemScore=res.data.data[0].problem.score;
                }else{
                    this.$store.state.loading=false;
                }
            }).catch((error)=> {
                console.log(error)
            })
