//      保存用户答案
        saveAnswer(){
            localStorage['answer']=this.answer;
        },

//      点击参考资料按钮
        viewRef(){
            this.$router.push('/Reference');
        },

//      获取组长id
        getGroupId(){
            this.$axios.post('/api/group-start',{
              "design_id":this.project_id
            }).then((res)=>{
//              console.log(res)
                if(res.data.data[0].name==='此演练不分组'){//演练不分组
                    this.groupId=res.data.data[0].id;
                }else{//演练分组
                    for(let i in res.data.data){
                        if(this.userId===res.data.data[i].group_user){//此人是组长
                            this.groupId=res.data.data[i].id;
                        }
                    }
                }
            }).catch((error)=>{
                console.log(error)
              })
