//      关闭提交答案弹框
        boxHide(){
            this.isShow=false;
        },

//      点击提交答案
        determine(){
            this.$axios.post('/api/play-problem',{
                "node_id":this.nodeId
            }).then((res)=>{
//                console.log(res)
                this.problemId=res.data.data[0].problem.id;
                this.submitAnswer();
            }).catch((error)=> {
//              console.log(error)
            })

