//      提交答案
        submitAnswer(){
//        	console.log('答案是'+this.answer)
            this.$axios.post('/api/answer-add',{
                "group_id":this.groupId,
                "user_id":this.userId,
                "problem_id":this.problemId,
                "answer":this.answer
            }).then((res)=> {
//	            console.log('答案提交了')
//                console.log(res)
                if(res.data.message=='此题你已答过！'){
                    this.$message({
                        message: '此题你已答过！',
                        type: 'warning',
	                    duration:1000,
                    });
                    this.isShow=false;
                }else if(res.data.message=='请组长答题！'){
                    this.$message({
                        message: '请组长答题',
                        type: 'warning',
	                    duration:1000,
                    });
                    this.isShow=false;
                }else{
                    this.$message({
                        message: '提交成功',
                        type: 'success',
	                    duration:1000,
                    });
                    this.isShow=false;
                }
                this.isSubmit = 2;
                localStorage['isSubmit'] = 2;
            }).catch((error)=> {
                  console.log(error)
                })
