//      获取分组
        getGroup(){
            this.$axios.post('/api/group-start',{
                "design_id":this.project_id
            }).then((res)=>{
//                console.log(res)
                this.groupList=res.data.data;
            }).catch((error)=>{
              console.log(error)
            })
