//       获取所有组的答案
        getAllAnswers(){
	        //开始答题环节结束，进入显示所有组答案环节，将用户答案、是否提交状态重置
	        localStorage['answer'] = '';
	        this.answer = '';
            localStorage['isSubmit'] = 1;
            this.isSubmit = 1;

	        //显示答案环节
            let arrGroup = [];
            this.allAnswer = [];
            let arr = this.groupList;
            this.$axios.post('/api/play-answer',{
                "problem_id":this.subject.id
            }).then((res)=> {
//                console.log(res)
                if(arr[0].name=='此演练不分组'){//演练不分组，一人一组
                    let array=res.data.data;
                    for(let i in res.data.data){
                        array[i].show=true;
                        array[i].name=res.data.data[i].user.name;
                        array[i].answer_id=res.data.data[i].id;
                        array[i].mark='';
                        array[i].isMarkRight='';
                    }
                    arrGroup=array;
                }else{//演练分组
                    for(let x in res.data.data){
                        for(let y in arr){
                            if(res.data.data[x].user_id===arr[y].group_user){
                                arr[y].show=true;
                                arr[y].answer=res.data.data[x].answer;
                                arr[y].answer_id=res.data.data[x].id;
                                arr[y].mark='';
                                arr[y].isMarkRight='';
                            }
                        }
                    }
                    arrGroup=arr;
                }

                //去除签到，但是没有再次登录账号进行回答问题的账号
                let lastArr=[];
                for(var i in arrGroup){
                    if(arrGroup[i].answer_id){
                        lastArr.push(arrGroup[i]);
                    }
                }
                this.allAnswer=lastArr;

                if(this.allAnswer==''&&this.showTitleA=='开始打分'){
	                document.getElementById('subScoreId').style.display="none";
                }
                //所有组答案显示隐藏切换
                if( this.allAnswer!=''){
                    this.$nextTick(()=>{
                        let all = document.getElementsByClassName('answer');
                        for(let i = 0; i < all.length; i++){
                            let spanH =  all[i].childNodes[0].childNodes[0];
                            if(spanH.offsetHeight < 60){
                                all[i].setAttribute('class', 'answer hide_btn');
                            }
                        }
                    })
                }

                this.$store.state.loading=false;
            }).catch((error)=> {
                console.log(error)
              })
        },

//        监听导演端是否显示答案
        getReferenceStatus(){
        	this.$axios.post('/api/problem-status',{
        		"problem_id":this.subject.id
            }).then((res)=>{
//		        console.log(res.data.data.status);
                if(res.data.data.status == 1){//显示参考答案
                    this.isShowReference = true;

                    if(!this.isLength){
	                    this.$nextTick(()=>{
		                    console.log(5)
		                    let ref = document.getElementsByClassName('reference_content');
		                    console.log(ref)
		                    if(ref.length !=0){
			                    console.log(6)
			                    let spans = ref[0].childNodes[0].childNodes[0];
			                    if(spans.offsetHeight < 60){
				                    ref[0].setAttribute('class', 'reference_content have_not');
			                    }
		                    }
	                    })
	                    this.isLength = true;
                    }
                }else{//不显示参考答案
	                this.isShowReference = false;
                }
	        }).catch((error)=>{
        		console.log(error);
            })
        },

//      是否显示所有组答案详情
        showDet(val,status){
            for(let i in this.allAnswer){
                if(this.allAnswer[i].id === val){
                    if(status){
                        this.allAnswer[i].show = false;
                    } else {
                        this.allAnswer[i].show = true;
                    }
                }
            }
            //重新渲染v-show

            if(this.showTitleA == '显示所有组答案'){
	            this.showTitleA = '';
	            this.showTitleA = '显示所有组答案';
            }else if(this.showTitleA == '开始打分'){
	            this.showTitleA = '';
	            this.showTitleA = '开始打分';
            }else if(this.showTitleA == '开始点评'){
	            this.showTitleA = '';
	            this.showTitleA = '开始点评';
            }
        },

//      开始打分,提交分数
        setScores(){
            let paramy=[];
            this.$axios.post('/api/play-problem',{
              "node_id":this.nodeId
            }).then((res)=>{
//                console.log(res)
                this.problemId=res.data.data[0].problem.id;

                //重新渲染错误提示，下面的第一个for循环是为了让没填分数的组，在点击提交答案的时候显示'请输入正确的分数提示'
	            this.showTitleA = '';
	            this.showTitleA = '开始打分';

                for(var i in this.allAnswer){
                	if(this.allAnswer[i].isMarkRight===''){
		                this.allAnswer[i].isMarkRight=true;
                    }
                }

                for(var  i in this.allAnswer){//对打的分数进行验证，
                    if(this.allAnswer[i].isMarkRight==true){//如果有一个分数没有通过验证,那么就让isValidation为true
                        this.isValidation=true;
                        break;
                    }else{
                       this.isValidation=false;
                    }
                }

                if(this.isValidation==false){//分数全部通过验证，可以提交分数
//                   console.log('提交分数了')
                    this.$axios.post('/api/play-answer',{
                    "problem_id":this.problemId
                    }).then((res)=> {
//                      console.log(res)
                    for(let i in this.allAnswer){
                        paramy.push({answer_id:this.allAnswer[i].answer_id,score:this.allAnswer[i].mark});
                    }
                        this.$axios.post('/api/score-add',{
                            scores:paramy
                        }).then((res)=>{
//                            console.log(res)
                            if(res.data.message==='成功'){
                                this.$message({
                                    message: '评分成功',
                                    type: 'success'
                                });
                                this.isSetscore = true;
                            }else if(res.data.message==='请勿重复评分！'){
                                this.$message({
                                    message: '请勿重复评分',
                                    type: 'warning'
                                });
                            }else{
                                this.$message.error('评分失败');
                            }

                            //评分完成，按钮设置为灰色，不可点击
	                        document.getElementById('subScoreId').style.background = '#727173';
	                        document.getElementById("subScoreId").disabled = true;
	                        document.getElementById("subScoreId").style.cursor = 'not-allowed';

                        }).catch((error)=>{
                            console.log(error)
                        })
                    }).catch((error)=> {
                    console.log(error)
                })
                }
            }).catch((error)=> {
                console.log(error)
            })
        },

//      评分验证
        scoreIpt(index,marks){
            for(let i in this.allAnswer){
                if(index === this.allAnswer[i].id){
//                    let myTag =  /^\-?\d+(\.\d+)?$/;
                    let myTag =  /^\d+(\.\d+)?$/ ;//验证非负数
                    if (!myTag.test(marks)) {//没有通过验证
                        this.allAnswer[i].isMarkRight = true;
                    }else {//通过验证
                        if (marks>this.problemScore){
                            this.allAnswer[i].isMarkRight = true;
                        }else{
                            this.allAnswer[i].isMarkRight = false;
                        }
                    }
                }
            }

            this.showTitleA = '';
            this.showTitleA = '开始打分';
        },

//      显示环节分数
        getNodeScore(){
            let arr=[];
            let lastArr=[];
	        this.scoreLinkGroup=[];
            //获取problemId
            this.$axios.post('/api/play-problem',{
              "node_id":this.nodeId
            }).then((res)=>{
//              console.log(res)
                this.problemId=res.data.data[0].problem.id;
                arr=this.groupList;
                //获取分数
                this.$axios.post('/api/score-node',{
                    "design_id":this.project_id,
                    "problem_id":this.problemId
                }).then((res)=>{
//                console.log(res)
                    let scoreArr=[];
                    if(this.groupList[0].name==='此演练不分组'){//一人一组
                        let array=res.data.data;
//                        console.log(array)
                        for(let i in res.data.data){
                          array[i].name=res.data.data[i].user_name;
                        }

                        scoreArr=array;
                    }else{//多人一组
                        for(let x in res.data.data){
                            for(let y in arr){
                                if(res.data.data[x].user_id===arr[y].group_user){
                                    arr[y].score=res.data.data[x].score;
                                }
                            }
                        }

                        scoreArr=arr;
                    }

                    //去除签到，但是没有再次登录账号进行回答问题的账号
                    for(let i in scoreArr){
                        if(scoreArr[i].score){
                            lastArr.push(scoreArr[i]);
                        }
                    }
                    this.scoreLinkGroup=lastArr;
                    this.showRank();
//	                this.$store.state.loading = false;
                }).catch((error)=>{
                    console.log(error)
                  })
                }).catch((error)=> {
                    console.log(error)
                })
        },

//      获取该演练的总分
        getTotalPoints(){
            this.scoreTotal=0;
            for(let i in this.linkBigList){
                this.$axios.post('/api/play-problem',{
                    node_id: this.linkBigList[i].id
                }).then((res) => {
//                    console.log(res);
                    let score = res.data.data[0].problem.score;
                    this.scoreTotal = this.scoreTotal + score;
                }).catch((error)=>{
                    console.log(error)
                })
            }
        },

//      显示排名
        showRank(){
            let arr =[];
	        this.allScore=[];
            this.$axios.post('/api/accord-rank',{
                "design_id":this.project_id
            }).then((res)=> {
//              console.log(res)
                arr=res.data.data;
                for(let i=0;i<res.data.data.length;i++){
                    if(i == 0){
                        arr[i].src='./static/first.png';
                    }else if(i == 1){
                        arr[i].src='./static/second.png';
                    }else if(i == 2){
                        arr[i].src='./static/third.png';
                    }else if(i == 3){
                        arr[i].src='./static/fourth.png';
                    }
                }

                this.allScore=arr;

                if(res.data.data.length>=4){
                    this.ranking='四'
                }else if(res.data.data.length == 3){
                    this.ranking='三'
                }else if(res.data.data.length == 2){
                    this.ranking='二'
                }else if(res.data.data.length == 1){
                    this.ranking='一'
                }

//                if(this.showTitleA == '结束演练'){
	                this.$store.state.loading = false;
//                }
            }).catch((error)=> {
                console.log(error)
            })
          },

        //全部答案li左边框随机色
        liRandomColor(index){
