//        判断文件是否为图片
        isImage(ext) {
            return ['png', 'jpg', 'jpeg', 'bmp', 'gif', 'webp', 'psd', 'svg', 'tiff'].
            indexOf(ext.toLowerCase()) !== -1;
        },

//        判断文件是否为视频
        isVedio(ext) {
            return ['mp4', 'rmvb', 'avi', 'ts'].
            indexOf(ext.toLowerCase()) !== -1;
        },

//        去重函数
        unique(array) {
            var obj = {};
            return array.filter(function(item, index, array) {
                return obj.hasOwnProperty(typeof item + JSON.stringify(item))
                  ? false
                  : (obj[typeof item + JSON.stringify(item)] = true);
            });
        },
    },

    watch:{
    	showTitleA:function(newVal, oldVal){
//            console.log('this.showTitleA从 '+ oldVal+'改变到了 ' + newVal)
            if(newVal == '显示所有组答案'&&oldVal == '开始答题'){
	            if(this.isSubmit == 1&&this.role == '参演'&& this.flag == false){//参演者未提交，并且倒计时未结束
		            console.log('导演切换环节，从开始答题到显示所有组答案')
		            this.determine();
		            setTimeout(this.getStep(),1500);
	            }
            }
        }
    },

    filters:{
	    grades: function(value) {
		    if (value == 0.0) {
			    return '0';
		    }else{
			    return parseFloat(value).toFixed(1);
            }
	    }
    }
