                        left: 50%;
                        margin-left: -5px;
                        margin-bottom: -5px;
                        width: 10px;
                        height: 10px;
                        border-left: 0;
                        border-top: 0;
                        transform:rotate(45deg);
                        -ms-transform:rotate(45deg); 	/* IE 9 */
                        -moz-transform:rotate(45deg); 	/* Firefox */
                        -webkit-transform:rotate(45deg); /* Safari 和 Chrome */
                        -o-transform:rotate(45deg); 	/* Opera */
                        background-color: #4a90e2;
                    }
                }
                //已经结束的环节
                .directLinkEnd{
                    background-color: #89cef5;
                    color: #fff;
                }
            }
            //倒计时
            .clock{
                width: 156px;
                height: 40px;
                margin-right: -25px;
                position: absolute;
                top: 0;
                right: 0;
                p{
                    font-size: 20px;
                    border-radius: 3px;
                    background-color: #f8ba54;
                    color: #ffffff;
                    height: 40px;
                    line-height: 40px;
                    i{
                        display: inline-block;
                        margin-left: 25px;
                        margin-right: 20px;
                        margin-top: 6px;
                        &:before{
                            font-size: 28px;
                        }
                    }
                    span{
                        float: right;
                        height: 40px;
                        width: 60px;
                        margin-right: 10px;
                        text-align: left;
                    }
                }
            }
            //参考答案按钮
            .referenceBtn{
                margin-right: -25px;
                float: right;
            }
        }
        .direct_main{
            width: 80%;
            margin: 10px 0 50px 46px;
            height: 600px;
            padding: 10px 15px 20px 10px;
            position: relative;
            //观看素材
            .material_play{
                background-color: #fff;
                height: 570px;
                padding-left: 18px;
                h3{
                    height: 45px;
                    font-size: 16px;
                    font-weight: 500;
                    line-height: 45px;
                    text-align: left;
                    color: #4a4a4a;
                }
                .text{
                    font-size: 16px;
                    font-weight: 500;
                    text-align: left;
                    color: #4a4a4a;
                }
                .video{
                    width: 90%;
                    height: 520px;
                    background-color: #000;
                    margin:0 auto;
                }
            }
            //观看题目
            .subject_play{
                background-color: #fff;
                padding-left: 18px;
                padding-bottom: 32px;
                padding-right: 75.8px;
                font-size: 16px;
                font-weight: 500;
                text-align: left;
                h3{
                  height: 45px;
                  line-height: 45px;
                  color: #4a4a4a;
                  font-size: 16px;
                  font-weight: 500;
                  text-align: left;
                }
                p{
                    color: #f5a623;
                }
                div{
                    margin-top: 17px;
                    color: #4a4a4a;
                }
            }

            //开始答题输入框
            .subject_answer{
              height: 395px;
              margin-top: 15px;
              border-radius: 5px;
              background-color: #fff;
            }

            //参考资料提交按钮
            .subject_button{
                height:75px;
                text-align: center;
                margin-top:15px;
                button{
                    width: 233.6px;
                    height: 42px;
                    border-radius: 3px;
                    background-color: #4a90e2;
                    margin-left: 34px;
                    font-size: 16px;
                    text-align: center;
                    color: #fff;
                }
            }

            //评分端／观看端正在答题显示
            .answerIng{
                position: absolute;
                top:50%;
                left:50%;
                margin-left:-98px;
                font-size: 30px;
                .loading{
                    width:100px;
                    position: absolute;
                    top:0;
                    left:50%;
                    margin-left: 98px;
                }
                .object{
                    width: 7px;
                    height: 7px;
                    background-color: #4a4a4a;
                    float: left;
                    margin-right: 20px;
                    margin-top: 20px;
                    -moz-border-radius: 50% 50% 50% 50%;
                    -webkit-border-radius: 50% 50% 50% 50%;
                    border-radius: 50% 50% 50% 50%;
                }
                #object_one {
                    -webkit-animation: object_one 1.5s infinite;
                    animation: object_one 1.5s infinite;
                }
                #object_two {
                    -webkit-animation: object_two 1.5s infinite;
                    animation: object_two 1.5s infinite;
                    -webkit-animation-delay: 0.25s;
                    animation-delay: 0.25s;
                }
                #object_three {
                    -webkit-animation: object_three 1.5s infinite;
                    animation: object_three 1.5s infinite;
                    -webkit-animation-delay: 0.5s;
                    animation-delay: 0.5s;
                }
                @-webkit-keyframes object_one {
                    75% { -webkit-transform: scale(0); }//根据原点缩放
                }
                @keyframes object_one {
                    75% {
                        transform: scale(0);
                        -webkit-transform: scale(0);
                    }
                }
                @-webkit-keyframes object_two {
                    75% { -webkit-transform: scale(0); }
                }
                @keyframes object_two {
                    75% {
                        transform: scale(0);
                        -webkit-transform:  scale(0);
                    }
                }
                @-webkit-keyframes object_three {
                    75% { -webkit-transform: scale(0); }
                }
                @keyframes object_three {
                    75% {
                        transform: scale(0);
                        -webkit-transform: scale(0);
                    }
                }
            }

            //确认提交弹框
            .model {
            position: fixed;
            z-index: 99;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: rgba(0, 0, 0, 0.699);
            display: table;
            .box {
                background-color: white;
                width: 422.4px;
                height: 326px;
                margin: 0 auto;
                position: relative;
                top: 180px;
                .boxHeader {
                height: 50px;
                background-color: #4a90e2;
                text-align: center;
                    span {
                        line-height: 50px;
                        font-size: 22px;
                        letter-spacing: -0.3px;
                        color: #ffffff;
                    }
                    i {
                        color: white;
                        font-size: 25px;
                        font-weight: 500;
                        position: relative;
                        left:130px;
                        top: 3px;
                        cursor: pointer;
                    }
                }

                .boxIpt {
                    margin: 25px 0 0 34px;
                    p{
                        margin-bottom: 15px;
                        line-height: 18px;
                        font-size: 16px;
                        color: #909090;
                        text-align: left;
                    }
                }
                .modelBtn {
                text-align: center;
                margin-top: 170px;
                    button {
                        position: relative;
                        left: 10px;
                        width: 231.6px;
                        height: 40px;
                        border-radius: 3px;
                        background-color: #50b4ec;
                        color: white;
                        cursor: pointer;
                        outline: none;
                        font-size: 16px;
                        letter-spacing: 4px;
                    }
                }
            }
        }
        //参考答案
        .show_reference{
            background-color: #fff;
            padding-left: 23px;
            min-height: 106px;
            margin-bottom: 10px;
            position: relative;
            .reference_title{
                height: 22px;
                font-size: 16px;
                width: 10%;
                text-align: left;
                color: #000000;
                position: absolute;
                top: 50%;
                left: 23px;
                margin-top: -11px;
            }
            .reference_content{
                display: inline-block;
                width: 85%;
                font-size: 16px;
                line-height: 1.75;
                text-align: left;
                color: #7d7c7d;
                padding-top: 15.4px;
                margin-left: 10%;
                span{
                    margin-bottom: 10px;
                    display: inline-block;
                    line-height: 25px;
                }
                .showAllReferenceContent{
                    height: 50px;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    display: -webkit-box;
                    -webkit-line-clamp: 2;
                    -webkit-box-orient: vertical;
                    margin-bottom: 10px;
                    line-height: 25px;
                }
                p{
                    font-size: 16px;
                    color: #4a90e2;
                    text-align: right;
                    padding-right: 10px;
                    cursor: pointer;
                }
            }
            .have_not{
                p{
                    display: none;
                }
            }
        }
        //所有参演组答案
        .all_answer{
            li{
                background-color: #fff;
                padding-left: 23px;
                min-height: 106px;
                margin-bottom: 10px;
                position: relative;
                div.group_name{
                    height: 22px;
                    font-size: 16px;
                    width: 10%;
                    text-align: left;
                    color: #000;
                    position: absolute;
                    top: 50%;
                    left: 23px;
                    margin-top: -11px;
                }
                div.answer{
                    display: inline-block;
                    width: 85%;
                    font-size: 16px;
                    line-height: 1.75;
                    text-align: left;
                    color: #7d7c7d;
                    padding-top: 15.4px;
                    margin-left: 10%;
                    .answer_roughly{
                      height: 50px;
                      overflow: hidden;
                      -webkit-box-orient: vertical;
                      margin-bottom: 10px;
                    }
                    .answer_detail{
                        font-size: 16px;
                        color: #4a90e2;
                        text-align: right;
                        padding-right: 10px;
                        cursor: pointer;
                    }
                    .setScore{
                        border:1px solid #4a90e2;
                        width:86.4px;
                        height:35px;
                        display: inline-block;
                        float:  right;
                        outline:none;
                        border-radius: 3px;
                        margin-bottom: 8px;
                        font-size: 16px;
                        text-align: center;
                        color: #9b9b9b;
                        margin-right: 8px;
                    }
                }
                .hide_btn{
                    p{
                        display: none;
                    }
                }
            }
        }

        .submit{
            width: 284px;
            height: 42px;
            margin: 0 auto;
            .submit_score{
                width: 284px;
                height: 42px;
                border-radius: 3px;
                background-color: #4a90e2;
                color: #fff;
            }
        }

        //开始打分
        .startScore{
            font-size: 30px;
            color: #4a4a4a;
            text-align: center;
            margin-bottom: 10px;
            position: relative;
            .loading1{
                width:100px;
                position: absolute;
                top:0;
                left:50%;
                margin-left: 98px;
            }
            .object{
                width: 7px;
                height: 7px;
                background-color: #4a4a4a;
                float: left;
                margin-right: 20px;
                margin-top: 20px;
                -moz-border-radius: 50% 50% 50% 50%;
                -webkit-border-radius: 50% 50% 50% 50%;
                border-radius: 50% 50% 50% 50%;
            }
            #object_one1 {
                -webkit-animation: object_one 1.5s infinite;
                animation: object_one 1.5s infinite;
            }
            #object_two1 {
                -webkit-animation: object_two 1.5s infinite;
                animation: object_two 1.5s infinite;
                -webkit-animation-delay: 0.25s;
                animation-delay: 0.25s;
            }
            #object_three1 {
                -webkit-animation: object_three 1.5s infinite;
                animation: object_three 1.5s infinite;
                -webkit-animation-delay: 0.5s;
                animation-delay: 0.5s;
            }
            @-webkit-keyframes object_one1 {
                75% { -webkit-transform: scale(0); }
            }
            @keyframes object_one1 {
                75% {
                    transform: scale(0);
                    -webkit-transform: scale(0);
                }
            }
            @-webkit-keyframes object_two1 {
                75% { -webkit-transform: scale(0); }
            }
            @keyframes object_two1 {
                75% {
                    transform: scale(0);
                    -webkit-transform:  scale(0);
                }
            }
            @-webkit-keyframes object_three1 {
                75% { -webkit-transform: scale(0); }
            }
            @keyframes object_three1 {
                75% {
                    transform: scale(0);
                    -webkit-transform: scale(0);
                }
            }
        }

        //开始点评
        .start_comment{
          width: 250px;
          height: 250px;
          float: right;
            /*position: relative;*/
            /*bottom: 100px;*/
            /*right: 0;*/
            /*margin-right:15px;*/
        }

          //观看直播
        .viewLive{
            width:100%;
            height:100%;
            /*background-color: coral;*/
        }

        //展示得分
        .get_score{
            width: 789px;
            min-height: 585px;
            background-color: #ffffff;
            box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.35);
            border: solid 1px #afafaf;
            margin: 0 auto 50px;
            padding: 0 0 30px;
            //得分排行榜
            .score_title{
                height: 52px;
                line-height: 52px;
                background-color: #51abea;
                color: #fff;
                font-size: 20px;
                font-weight: 600;
                text-align: center;
            }
            //本环节得分
            p.score_link{
                font-size: 18px;
                font-weight: 600;
                color: #4a90e2;
                height: 52px;
                line-height: 52px;
                text-align: center;
            }
            ul.score_group {
                width: 788px;
                min-height: 89px;
                background-color: #f1f1f1;
                li{
                    display: inline-block;
                    width: 25%;
                    margin-bottom: 10px;
                    text-align: center;
                    p{
                        width: 100%;
                        height: 25px;
                        font-size: 18px;
                        color: #000000;
                        margin-top: 13px;
                        margin-bottom: 14px;
                    }
                    span{
                        width: 96px;
                        height: 25px;
                        font-size: 18px;
                        color: #d0021b;
                    }

                }
            }
            //本次演练总排名
            h3{
                font-size: 18px;
                font-weight: 600;
                text-align: center;
                color: #4a90e2;
                margin: 39px 0 29px;
            }
            .score_main{
                display: inline-block;
                .every_group{
                    height: 50px;
                    margin-bottom: 20px;
                    margin-left: 80px;
                    img{
                        width: 40px;
                        display: inline-block;
                        vertical-align: middle;
                        margin-bottom: 10px;
                        margin-right: 20px;
                    }
                    span{
                        display: inline-block;
                        font-size: 20px;
                        color: #000000;
                        margin-right: 20px;
                        b{
                          display: inline-block;
                          width: 20px;
                          text-align: center;
                          color: #4a90e2;
                        }
                    }
                    span.bac{
                        height: 25px;
                        line-height: 25px;
                        padding-right: 10px;
                        text-align: right;
                        background-color: #9b9b9b;
                        margin-bottom: -5px;
                        /*i{*/
                          /*display: inline;*/
                          /*font-size: 20px;*/
                          /*color: #000;*/
                        /*}*/
                    }
                }
            }
        }

        //进度条
        .el-progress{
            float: left;
            width: 300px;
            margin-top: 12px;
            .el-progress-bar__innerText{
                display: none;
            }
        }

        //结束演练
        .over_drill{
            width: 789px;
            min-height: 400px;
            background-color: #ffffff;
            box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.35);
            border: solid 1px #afafaf;
            margin: 20px auto 50px;
            padding: 0 0 30px;
            .over_drill_title{
                height: 52px;
                line-height: 52px;
                background-color: #51abea;
                color: #fff;
                font-size: 20px;
                font-weight: 600;
                text-align: center;
            }
            .the_fourth{
                font-size: 18px;
                font-weight: 600;
                color: #4a90e2;
                height: 52px;
                line-height: 52px;
                text-align: center;
            }
            .score_main{
                display: inline-block;
                .every_group{
                    height: 50px;
                    margin-bottom: 20px;
                    margin-left: 80px;
                    img{
                        width: 40px;
                        display: inline-block;
                        vertical-align: middle;
                        margin-bottom: 10px;
                        margin-right: 20px;
                    }
                    span{
                        display: inline-block;
                        font-size: 20px;
                        color: #000000;
                        margin-right: 30px;
                        b{
                            display: inline-block;
                            width: 20px;
                            text-align: center;
                            color: #4a90e2;
                        }
                    }
                    span.bac{
                        height: 25px;
                        line-height: 25px;
                        padding-right: 10px;
                        text-align: right;
                        background-color: #9b9b9b;
                    }
                }
            }
        }
        }
        //直播
        .video-js{
            width: 100%;
            height: 500px;
            .vjs-button.vjs-playing{
                content: normal;
            }

            //暂停
            .vjs-icon-pause:before{
                content: "\e607"!important;
            }
        }
        /*.video-js .vjs-big-play-button:before, .video-js .vjs-play-control:before, .vjs-icon-play:before{*/
          /*content: ""!important;*/
        /*}*/
        .el-textarea__inner,.el-textarea__inner:focus,.el-textarea__inner:hover{
          border:1px solid #fff;
        }

        .vjs-error .vjs-error-display:before{
            content:"";
        }

        .vjs-error .vjs-error-display .vjs-modal-dialog-content{
            display:none;
        }

        .video-js .vjs-big-play-button{
            display:none;
        }
    }
</style>



